import React from "react";

import { graphql, useStaticQuery } from "gatsby";

import Layout from "../components/layout_inner";
import CoverImg from "../components/cover_img";

export default () => {
  const data = useStaticQuery(graphql`
    query {
      file(relativePath: { eq: "main.jpg" }) {
        childImageSharp {
          fluid(quality: 100, maxWidth: 1920) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  return (
    <Layout>
      <CoverImg fluid={data.file.childImageSharp.fluid} alt="photo" />
    </Layout>
  );
};
